.app__footer {
    padding: 5rem 10rem;
    background-color: #4C5159;

    height: 156px;
}

.app__footer-icons{
    display: flex;
    justify-content: space-between;

}

.footer-icon {
    display: flex;
    justify-content: space-between;

    padding: 12px;
}

.icons {
    display: flex;
    justify-content: center;

    padding: 6px;
    width: 40px;
    height: 35px;

    border-radius: 50%;
}